body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.myCourses {
  margin-left: 10px;
}

.titleStyle {
  color: #2c2e33;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  width: 1100px;
  text-align: center;
  margin-bottom: 10px;
}

.sectionSelectors {
  margin-bottom: 20px;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  width: 1100px;
  text-align: center;
}

.sectionTitle {
  margin-bottom: 20px;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  width: 1100px;
  text-align: center;
}

.timetable {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 50px repeat(5, 200px);
  grid-template-rows: repeat(20, 50px);
}

.titleWrapper {
  display: grid;
  grid-column: 1 / 7;
  grid-gap: 10px;
  grid-template-columns: 50px repeat(5, 200px);
  margin-bottom: 10px;
  border-bottom: 1px solid black;
  justify-items: center;
}

.timeWrapper {
  display: grid;
  grid-row: 2 / 21;
  grid-gap: 10px;
  grid-template-rows: repeat(20, 50px);
}

p {
  margin: 0;
  padding: 0;
}

.tableElement {
  border: 2px solid black;
  padding: 4px;
}

.tableElement:hover{
  -webkit-animation: pulse 0.3s linear 1;
          animation: pulse 0.3s linear 1;
  cursor: pointer;
}

@-webkit-keyframes pulse{
  50%  {-webkit-transform: scale(0.97);transform: scale(0.97);}
}

@keyframes pulse{
  50%  {-webkit-transform: scale(0.97);transform: scale(0.97);}
}

